body {
    margin: 0;
}

.slider {
    max-width: 100%;
    text-align: center;
    max-height: 100vh;
}

.fSliderWrapper {
    position: relative;
    margin: 0 auto;

    .fSlider {
        position: relative;
        width: 0;
        //height: 0;
        overflow: hidden;
        margin: 0 auto;

        .fSliderTrack {
            position: absolute;
            top: 0;
            left: 0;
            cursor: pointer;

            img {
                -webkit-user-drag: none;
                -khtml-user-drag: none;
                -moz-user-drag: none;
                -o-user-drag: none;
                user-drag: none;
                -webkit-user-select: none;
                -khtml-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
                -webkit-touch-callout: none;
                -ms-touch-action: pan-y;
                touch-action: pan-y;
                -webkit-tap-highlight-color: transparent;
            }

            .sliderItem {
                box-sizing: border-box;
                display: inline-block;
                vertical-align: middle;
                background-size: 100% auto;
                background-attachment: fixed;
                background-position: center center;

                &.slide1 {
                    background-image: url('https://innerhealthcenter.files.wordpress.com/2012/11/lemons.jpg');
                }

                &.slide2 {
                    background-image: url('http://i.huffpost.com/gen/2930708/images/o-GMO-ORANGES-facebook.jpg');
                }

                &.slide3 {
                    background-image: url('http://blogs.kcrw.com/goodfood/wp-content/uploads/2013/05/watermelon.jpeg');
                }

                &.slide4 {
                    background-image: url('http://lghttp.32478.nexcesscdn.net/80E972/organiclifestylemagazine/wp-content/uploads/2013/09/Blueberries-.jpg');
                }

                &.slide5 {
                    background-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/f/f2/Limes.jpg/1200px-Limes.jpg');
                }

                img {
                    display: block;
                }

                span {
                    cursor: default;
                    -webkit-touch-callout: none;
                    -webkit-user-select: none;
                    -khtml-user-select: none;
                    -moz-user-select: none;
                    -ms-user-select: none;
                    user-select: none;
                    display: block;
                    width: 100vw;
                    height: 100vh;
                    top: 12vw;
                    font-size: 10vw;
                    line-height: 100vh;
                    letter-spacing: 1vw;
                    font-family: Impact, Charcoal, sans-serif;
                    text-shadow: 5px 5px 0px #ffffff;
                }
            }
        }
    }

    a[class|="fArrow"] {
        position: absolute;
        display: block;
        top: 50%;
        margin-top: -20px;
        width: 40px;
        height: 40px;
    }

    .fArrow-prev {
        left: 20px;
        border-bottom: 6px solid #ffffff;
        border-left: 6px solid #ffffff;
        transform: rotate(45deg);
        transition: left 0.5s;

        &:hover {
            left: 15px;
        }
    }

    .fArrow-next {
        right: 20px;
        border-bottom: 6px solid #ffffff;
        border-right: 6px solid #ffffff;
        transform: rotate(-45deg);
        transition: right 0.5s;

        &:hover {
            right: 15px;
        }
    }
}